#searchHotel article {
    display: flex;
    width: 80%;
    margin: 0 auto;
    padding: 10px;
    gap: 10px;
}

#searchHotel>article form {
    width: 100%;
    display: flex;
    margin-top: -40px;
    position: relative;
}

#searchHotel>article form div:nth-child(1) {
    flex-basis: 40%;
}

#searchHotel>article form div:nth-child(2) {
    flex-basis: 20%;
}

#searchHotel>article form div:nth-child(3) {
    flex-basis: 30%;
}

#searchHotel>article form div:nth-child(4) {
    flex-basis: 10%;
}

#searchHotel>article form input,
#searchHotel>article form select {
    width: 100%;
    padding: 13px;
    border: 1px solid #9e9e9e47;
    height: 50px;
    outline: none;
}

input:focus {
    border-bottom: 2px solid #673ab7 !important;
}

.dateSearch {
    display: flex;
}

#searchHotel>article form button {
    background-color: #673ab7;
    color: #fff;
    border: 1px solid #673ab7;
    padding: 15px 38px;
    display: inline-block;
    cursor: pointer;
    font-weight: 900;
    height: 50px;
    transition: ease all 0.8s;
}

#searchHotel>article form button:hover {
    background-color: deeppink;
    border: 1px solid deeppink;
}

#searchHotel>article form div span {
    color: #999;
    position: absolute;
    padding: 15px 6px;
}